
@import "../../global.scss";

.testomonials {
    background-color: white;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;

    @include mobile {
        justify-content: space-around;               
    }

    h1 {
        font-size: 50px;

        @include mobile {
            font-size: 20px;           
        }
    }
    .container {
        width: 100%;
        height: 90%;
        display: flex;
        justify-content: center;
        align-items: center;

        @include mobile {
            flex-direction: column;   
            height: 100%;
        }

        .card {
            width: 250px;
            height: 70%;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 20px;
            cursor: pointer;
            transition: all 1s ease;

            @include mobile {
                height: 180px;      
                margin: 10px 0; 
            }
            
            &.featured {
                width: 300px;
                height: 75%;
                margin: 0 30px;

                @include mobile {
                    width: 250px;
                    height: 180px;
                    margin: 1px;   
                }
            }
            &:hover {
                transform: scale(1.1);
            }

            .top {
                display: flex;
                justify-content: center;
                align-items: center;
                img {
                    &.left, &.right {
                        height: 25px;
                    }
                    &.user {
                        height: 30px;
                        width: 30px;
                        border-radius: 50%;
                        object-fit: cover;
                        margin: 0 30px;

                        @include mobile {
                            height: 40px;
                            width: 40px;
                        }
                    }
                }
            }
            .center {
                padding: 10px;
                border-radius: 10px;
                background-color: rgb(247, 241, 242);
            
                @include mobile {
                    font-size: 12px;
                    padding: 5px;   
                }
            }
            .bottom {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                h3 {
                    margin-bottom: 5px;
                    @include mobile {
                        font-size: 14px;
                    }
                }
                h4 {
                    color: grey;
                    @include mobile {
                        font-size: 13px;
                    }
                }
            }
        }
    }
}